.contact {
	position: relative;
	background: linear-gradient(180deg, #F8F8F8 0%, #E7E7E7 100%);
	position: relative;
	padding: rem(80) 0;

	@include min540 {
		padding: 0 0 rem(80) 0;
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: rem(-70);
		width: rem(251);
		height: rem(431);
		background: url(../img/section4_left_decor.png) no-repeat;
		background-size: 100%;

		@include min540 {
			display: none;
		}
	}

	&::after {
		content: "";
		position: absolute;
		top: rem(130);
		right: rem(-60);
		width: rem(381);
		height: rem(586);
		background: url(../img/section4_right_decor.png) no-repeat;
		background-size: 100%;

		@include min540 {
			width: rem(354);
			height: rem(384);
			top: auto;
			bottom: 0;
			right: 0;
			background: url(../img/section4_right_decor_x1.png) no-repeat;
			background-size: 100%;
		}
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&::before {
			content: "";
			position: absolute;
			bottom: rem(-359);
			left: 0;
			width: rem(899);
			height: rem(939);
			background: url(../img/footer_decor1.png) no-repeat;
			background-size: 100%;
			z-index: 2;

			@include min540 {
				display: none;
			}
		}
	}

	&__left {
		position: relative;
		z-index: 3;
		margin-left: rem(50);
		max-width: rem(410);

		@include min540 {
			margin-left: 0;
			max-width: 100%;
		}

		h2,h3 {
			color: var(--c-text-green);
			margin-bottom: rem(45);

			@include min540 {
				text-align: center;
			}
		}

		.item {
			display: flex;
			flex-wrap: wrap;

			&__icon {
				width: rem(37);
				height: rem(37);
				border-radius: 50%;
				background-color: var(--c-green);
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: rem(11);

				margin-bottom: rem(48);

				&:last-child {
					margin-bottom: 0;
				}
			}

			&__text {
				display: flex;
				flex-direction: column;

				b {
					font-size: rem(20);

				}

				p {
					font-size: rem(18);
				}

				b, p {
					font-family: var(--f-help);
					color: var(--c-text-green);

					@include min540 {
						color: var(--c-gray);
					}
				}
			}
		}
	}

	&__right {
		position: relative;
		z-index: 3;
		margin-right: rem(38);

		width: rem(520);
		height: rem(429);
		border-radius: rem(13);
		overflow: hidden;

		@include min540 {
			width: 100%;
			height: rem(250);
			margin-right: 0;
		}
	}
}