.footer {
	padding: rem(52) 0;
	background: linear-gradient(180deg, rgba(211, 211, 211, 0.1) 0%, rgba(196, 196, 196, 0.1) 100%);
	position: relative;
	z-index: 2;

	

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		backdrop-filter: blur(5px);

		z-index: 1;

		@include min768 {
			display: none;
		}
	}

	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		left: rem(-40);
		width: rem(484);
		height: rem(447);
		background: url(../img/footer_decor2.png) no-repeat;
		background-size: 100%;
		z-index: -1;

		@include min768 {
			display: none;
		}

	}

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		position: relative;
		z-index: 2;
	}

	&__left {
		padding-left: rem(30);

		@include min768 {
			padding-left: 0;
		}

		ul {
			max-width: rem(480);
			@include min768 {
				max-width: 100%;
			}
			li {
				color: var(--c-gray);
				font-size: rem(24);
				font-weight: 300;

				@include min768 {
					font-size: rem(18);
				}

				b {
					font-weight: bold;
				}

				a {
					&:hover {
						text-decoration: underline;
					}
				}

				&:first-child {
					margin-bottom: rem(30);

					b {
						color: var(--c-text-green);
					}
				}

				&:last-child {
					margin-top: rem(30);
				}
			}
		}
	}

	&__right {
		display: flex;
		flex-direction: column;
		padding-right: rem(50);

		@include min768 {
			padding-right: 0;
			margin-top: rem(32);
		}

		b {
			margin-bottom: rem(20);
			color: var(--c-gray);
			font-size: rem(24);

			@include min768 {
				font-size: rem(20);
			}
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(26);

			li {
				margin-right: rem(12);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}