.header {
	position: relative;
	// height: rem(1423);
	background: url(../img/header_bg.png) no-repeat top center;
	background-size: cover;

	padding-bottom: rem(42);

	&::before {
		content: "";
		position: absolute;
		bottom: rem(-140);
		right: 0;
		width: rem(649);
		height: rem(715);
		background: url(../img/header_right_decor.png) no-repeat;
		background-size: 100%;
		z-index: 1;

		@include min1150 {
			right: rem(-80);
		}

		@include min {
			display: none;
		}

	}

	.button-orange.smart {
		display: none;

		@include min {
			display: flex;
			margin-top: rem(64);
			padding: rem(11) rem(37);
			font-size: rem(18);
		}
	}

	&__top {
		padding-top: rem(32);
		margin-bottom: rem(100);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		margin-right: rem(-100);

		@include min1320 {
			margin-right: 0;
			margin-bottom: rem(42);
		}

		.left {
			margin-left: rem(36);
			span {
				font-size: rem(24);
				font-weight: 300;
				color: var(--c-text-green);
			}

			@include min {
				display: none;
			}
		}

		.logo {
			margin-left: rem(80);

			@include min {
				margin-left: auto;
				margin-right: auto;
			}
		}

		.right {
			@include min {
				display: none;
			}
			.button-green {


				span {
					font-size: rem(24);
					font-weight: 300;
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.left {
			position: relative;
			z-index: 1;
			display: flex;
			flex-direction: column;
			margin-left: rem(85);

			@include min1150 {
				margin-left: 0;
			}

			@include min {
				padding-left: rem(60);
				padding-right: rem(60);
			}

			@include min540 {
				padding-left: 0;
				padding-right: 0;
			}

			h1 {
				max-width: rem(500);
				margin-bottom: rem(34);

				font-weight: bold;
				// font-size: rem(36);
				background: var(--c-green-g);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;

				@include min1150 {
					max-width: rem(420);
				}

				@include min {
					text-align: center;
					max-width: 100%;
					margin-bottom: rem(20);
				}

				@include min540 {
					// font-size: rem(18);
				}
			}

			p {
				max-width: rem(530);
				margin-bottom: rem(20);

				@include min1150 {
					max-width: rem(420);
				}

				@include min {
					display: none;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			.button-green {
				padding-left: rem(56);
				padding-right: rem(56);
				align-self: flex-start;

				margin-top: rem(36);

			}

			.desktop {
				@include min {
					display: none;
				}
			}

			.smart {
				display: none;

				@include min {
					display: flex;
				}
			}

			.button-green.smart {
				align-items: center;
				padding: rem(10) rem(13);
				background: var(--c-green-g);
				align-self: center;
				.icon {
					margin-right: rem(15);
				}

				span {
					font-size: rem(18);
				}
			}
		}

		.right {
			position: relative;
			margin-top: rem(68);

			@include min {
				display: flex;
				justify-content: center;
				width: 100%;
			}
			
			.image {
				position: relative;
				width: rem(480);
				height: rem(480);
				border-radius: 50%;
				background: var(--c-oragne-g);

				// display: flex;
				// align-items: center;
				// justify-content: center;

				@include min1150 {
					width: rem(380);
					height: rem(380);
				}

				@include min480 {
					width: rem(300);
					height: rem(300);
				}

				img {
					position: absolute;
					top: rem(-40);
					left: rem(-100);
					max-width: rem(642);
					// height: rem(530);

					@include min1150 {
						max-width: rem(540);
						// height: rem(380);
					}

					@include min480 {
						left: rem(-60);
						max-width: rem(420);
					}
				}
			}
		}
	}
}