.advantages {
	position: relative;
	
	clip-path: ellipse(150% 100% at 50% 100%);

	@include min {
		clip-path: none;
	}

	&__wrap {
		position: relative;
		padding-top: rem(60);
		padding-bottom: rem(46);

		background: var(--c-green-g);

		overflow: hidden;

		clip-path: ellipse(150% 100% at 50% 0);

		@include min {
			clip-path: none;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: rem(1400);
			height: rem(573);
			background: url(../img/section2_decor.png) no-repeat;
			background-size: 100%;
			z-index: 1;

			
		}



		
	}

	&::after {
		content: "";
		position: absolute;
		bottom: rem(-280);
		left: rem(-80);
		width: rem(569);
		height: rem(712);
		background: url(../img/section2_left_decor.png) no-repeat;
		background-size: 100%;
		z-index: 1;

		@include min1150 {
			left: rem(-140);
			// height: rem(380);
		}

		@include min768 {
			display: none;
		}
	}

	&__title {
		position: relative;
		z-index: 2;
		margin-bottom: rem(50);
		h2 {
			text-align: center;
			color: white;
			margin-bottom: rem(13);
			font-size: rem(32);
			font-weight: bold;

			@include min540 {
				font-size: rem(18);
				margin-bottom: rem(22);
				padding: 0 rem(20);
			}
		}

		p {
			font-size: rem(24);
			color: white;
			text-align: center;

			@include min540 {
				font-size: rem(14);
				max-width: rem(226);
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	&__items {
		position: relative;
		z-index: 2;
		display: flex;
		flex-wrap: wrap;

		padding-left: rem(60);
		padding-right: rem(60);

		@include min768 {
			justify-content: center;
		}

		@include min380 {
			padding-left: rem(30);
			padding-right: rem(30);
		}

		.item {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-right: rem(46);
			width: calc((100% - 12rem) / 5);

			@include min768 {
				width: calc((100% - 12rem) / 3);
				margin-bottom: rem(20);

				&:nth-child(3) {
					margin-right: 0;
				}
			}

			@include min540 {
				width: 100%;
				margin-right: 0;

				flex-direction: row;
				flex-wrap: wrap;
				justify-content: flex-start;

				margin-bottom: rem(24);

			}

			&:last-child {
				margin-right: 0;

				@include min540 {
					margin-bottom: 0;
				}
			}

			img {
				max-width: rem(75);
				margin-bottom: rem(34);

				@include min540 {
					max-width: rem(50);
					margin-right: rem(18);
					margin-bottom: 0;

					// margin-right: auto;
				}
			}

			b, p {
				color: white;
				text-align: center;
			}
			b {
				font-weight: bold;
				margin-bottom: rem(16);

				height: rem(40);
				display: flex;
				align-items: center;

				@include min540 {
					font-size: rem(14);
					height: auto;
					margin-bottom: 0;

					max-width: rem(144);
					text-align: left;

					margin-right: auto;
				}
			}

			p {
				font-size: rem(12);
				line-height: rem(14);

				@include min540 {
					display: none;
				}
			}
		}
	}

	.button-white {
		position: relative;
		z-index: 2;
		margin-top: rem(28);

		@include min768 {
			display: none;
		}
	}
}