.info_text {
	position: relative;

	@include min768 {
		display: none;
	}


	&::before {
		content: "";
		position: absolute;
		top: rem(-240);
		right: rem(-50);
		width: rem(1448);
		height: rem(871);
		background: url(../img/section3_right_decor.png) no-repeat;
		z-index: 1;

		@include min1150 {
			right: rem(-180);
			// height: rem(380);
		}
	}
}

.info_text__wrap {
	position: relative;
	padding-top: rem(80);
	padding-bottom: rem(70);
	
	
	background: linear-gradient(270.5deg, #4AA016 19.05%, #1AD060 66.47%);

	clip-path: ellipse(115% 100% at 50% 100%);

	.container {
		padding-left: rem(140);

		@include min1150 {
			padding-left: 0;
		}
	}

