:root {
	// --var: ....
	--f-default: 'Gilroy';
	--f-help: 'Montserrat';
	--c-text-green: #467B35;
	--c-gray: #5E605E;
	--c-green: #00983A;
	--c-green-g: linear-gradient(180deg, #4AA016 0%, #00983A 100%);
	--c-green-g2: linear-gradient(180deg, #95C23F 0%, #00983A 100%);
	--c-oragne-g: linear-gradient(180deg, #E84219 0%, #E6701C 100%);

}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

.container {
	max-width: 1115px;
	margin: 0 auto;

	@include min1150 {
		width: calc(100% - 40px);
	}
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

p {
	font-weight: 300;
	color: var(--c-text-green);
}

h2,h3 {
	font-size: rem(32);

	@include min540 {
		font-size: rem(18);
	}
}

body {
	position: relative;
	font-size: rem(16);
	font-family: var(--f-default);
	// height: 100%;
	overflow-x: hidden;
	background-color: #F8F8F8;
	// -webkit-overflow-scrolling: touch;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button-green {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	background: var(--c-green-g2);
	padding: rem(15) rem(23);
	border-radius: rem(36);

	color: white;
	font-size: rem(20);
	font-weight: bold;

	.icon {
		margin-right: rem(12);
	}

	span {
		color: white;
	}
}

.button-orange {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	background: var(--c-oragne-g);
	padding: rem(15) rem(23);
	border-radius: rem(36);

	color: white;
	font-size: rem(20);
	font-weight: bold;

	.icon {
		margin-right: rem(12);
	}

	span {
		color: white;
	}
}

.button-white {
	padding: rem(14) rem(52);
	background-color: white;
	border-radius: rem(36);
	color: #00983A;
	font-size: rem(20);
	font-weight: 300;
}

.button-border {
	font-size: rem(24);
	padding: rem(14) rem(42);
	border-radius: rem(60);
	border: rem(2) solid var(--c-gray);
	color: var(--c-gray);

	@include min540 {
		font-size: rem(20);
	}
}