  color: white;
}

.button-orange {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  background: var(--c-oragne-g);
  padding: 0.9375rem 1.4375rem;
  border-radius: 2.25rem;
  color: white;
  font-size: 1.25rem;
  font-weight: bold;
}

