.info {
	// margin-top: rem(40);
	margin-bottom: rem(60);

	@include min {
		display: none;
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
	}

	&__left {
		@include min1150 {
			max-width: rem(400);
			// height: rem(380);
		}
	}

	&__right {
		max-width: rem(486);

		@include min1150 {
			max-width: rem(400);
			// height: rem(380);
		}

		h2 {
			font-size: rem(32);
			font-weight: bold;
			background: var(--c-green-g);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: rem(28);
		}

		p {
			font-size: rem(18);
			margin-bottom: rem(24);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}