.popup_overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(180deg, #FFAD0D 0%, rgba(253, 240, 214, 0) 100%);
	z-index: 100;


	// display: flex;
	align-items: center;
	justify-content: center;
	display: none;

	.popup {
		position: relative;
		width: rem(700);
		background-color: white;
		border-radius: rem(26);
		padding: rem(25) rem(36);

		@include min768 {
			width: calc(100% - 40px);
			padding: rem(12) rem(20);
		}

		&__close {
			position: absolute;
			top: rem(10);
			right: rem(10);

			img {
				width: rem(24);
				height: rem(24);
			}
		}

		&__title {
			margin-bottom: rem(36);
			text-align: center;

			@include min768 {
				margin-bottom: rem(20);
				padding-right: rem(32);
			}

			span {
				font-size: rem(28);
				font-weight: bold;
				background: var(--c-green-g);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;

				@include min768 {
					font-size: rem(18);
				}
			}
		}

		&__wrap {
			display: flex;
			flex-wrap: wrap;

			&-left {
				flex: 0 0 48%;
				max-width: 48%;
				margin-right: rem(40);

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
				}

				.title {
					margin-bottom: rem(8);
					font-size: rem(18);
					font-weight: bold;
					color: var(--c-gray);
					display: block;

					@include min768 {
						font-size: rem(16);
					}
				}
				
				.item {
					padding: rem(15) rem(28);
					border-radius: rem(13);
					background: linear-gradient(180deg, #FDFDFD 0%, #F4F4F4 100%);
					box-shadow: 5px 4px 9px 0px rgba(0, 0, 0, .15);
					
					display: flex;

					&__image {
						position: relative;
						// margin-right: rem(10);
						width: rem(118);
						height: rem(118);

						img {
							position: absolute;
							top: 0;
							left: rem(-30);
							max-width: rem(125);
							width: rem(125);
						}
					}

					&__text {
						text-align: right;
						// width: rem(160);
						flex: 0 0 rem(160);

						b {
							font-weight: bold;
							color: var(--c-green);
							display: block;
						}

						p {
							font-size: rem(12);
							color: var(--c-gray);
							margin-bottom: rem(27);
						}

						.price {
							font-weight: bold;
							color: var(--c-gray);
							margin-bottom: rem(12);
						}

						.count {
							height: rem(28);
							padding: 0 rem(40);
							border-radius: rem(38);

							font-weight: bold;
							color: white;
							background: linear-gradient(180deg, #4AA016 0%, #00983A 100%);
							display: flex;
							align-items: center;

							width: rem(127);
							margin-left: auto;
						}
					}
				}
			}

			&-right {
				position: relative;
				z-index: 2;
				flex: 0 0 42%;

				@include min768 {
					flex: 0 0 100%;
					margin-top: rem(20);
				}
			}

			.flex {
				flex: 0 0 100%;
				margin-top: rem(40);

				.button-green {
					padding: rem(14) rem(80);
					background: var(--c-green-g);
				}
			}
		}
	}
}

.field-set {
	display: flex;
	flex-direction: column;

	margin-bottom: rem(10);

	&:last-child {
		margin-bottom: 0;
	}

	label {
		font-size: rem(18);
		font-weight: bold;
		color: var(--c-gray);
		margin-bottom: rem(10);
	}

	.field {
		height: rem(58);
		width: 100%;
		background-color: #F2F2F2;
		border-radius: rem(16);
		padding: 0 rem(20);

		font-family: var(--f-help);
		font-weight: bold;
		color: var(--c-gray);
		font-size: rem(18);

		
		

		@include placeholder {
			color: var(--c-gray);
		}
	}
}

.popup_overlay.active {
	display: flex;
}