.shop {
	position: relative;
	padding-top: rem(80);
	padding-bottom: rem(45);

	@include min540 {
		padding-top: rem(40);
		padding-bottom: rem(30);
	}

	&::before {
		content: "";
		position: absolute;
		top: rem(-200);
		left: rem(-45);
		width: rem(1129);
		height: rem(883);
		background: url(../img/section3_left_decor.png) no-repeat;
		background-size: 100%;
		z-index: -1;

		
	}

	h2 {
		text-align: center;
		// font-size: rem(32);
		margin-bottom: rem(50);
		background: var(--c-green-g);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;

		@include min540 {
			max-width: rem(170);
			margin-bottom: rem(36);
			margin-left: auto;
			margin-right: auto;
		}
	}

	.swiper-container {
		z-index: 3;

		margin: rem(-20);
		padding: rem(20);

		.swiper-slide {
			height: auto;

			.item {
				height: 100%;
			}
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(20);
		position: relative;
		z-index: 2;

		@include min768 {
			justify-content: center;
		}

		
	}

	.item {
		width: 100%;
		// margin-right: rem(32);

		border-radius: rem(13);
		background: linear-gradient(180deg, #FDFDFD 0%, #F4F4F4 100%);
		box-shadow: 5px 4px 9px 0px rgba(0, 0, 0, .15);

		padding: rem(20) rem(30);

		// margin-bottom: rem(26);

		display: flex;
		flex-direction: column;

		// @include min768 {
		// 	width: calc((100% - 6rem) / 2);

		// 	&:nth-child(2n) {
		// 		margin-right: 0;
		// 	}
		// }

		// @include min540 {
		// 	position: relative;
		// 	width: 100%;
		// 	margin-right: 0;
		// 	display: none;

		// 	flex-direction: row;
		// 	flex-wrap: wrap;

		// 	&:first-child,
		// 	&:nth-child(2),
		// 	&:nth-child(3) {
		// 		display: flex;
		// 	}

		// 	&:nth-child(3) {
		// 		margin-bottom: 0;
		// 	}
		// }

		&__title {
			margin-bottom: rem(20);
			display: flex;
			flex-direction: column;
			align-items: center;

			@include min540 {
				order: 2;

				position: absolute;
				top: rem(15);
				right: rem(28);
				align-items: flex-end;
			}

			b {
				margin-bottom: rem(2);
				font-weight: bold;
				color: var(--c-green);
				text-align: center;
			}

			p {
				color: var(--c-gray);
				font-size: rem(12);
				text-align: center;
			}
		}

		&__image {
			display: flex;
			flex-direction: column;
			align-items: center;
			margin-bottom: rem(16);

			@include min540 {
				order: 1;
				margin-bottom: 0;

				max-width: rem(118);

			}

			img {
				margin-bottom: rem(16);

				@include min540 {
					margin-bottom: 0;
				}
			}

			b {
				color: var(--c-gray);

				@include min540 {
					position: absolute;
					bottom: rem(64);
					right: rem(28);
				}
			}
		}

		.flex {
			margin-top: auto;

			@include min540 {
				order: 3;
			}

		}

		.button-green {
			background: var(--c-green-g);
			padding: rem(6) rem(30);
			font-size: rem(16);
			font-weight: 300;

			@include min540 {
				position: absolute;
				bottom: rem(20);
				right: rem(21);
			}
		}

		&:nth-child(4n) {
			margin-right: 0;
		}
	}

	.swiper-pagination {
		position: relative;
		z-index: 2;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-top: rem(40);
		justify-content: center;

		bottom: auto !important;

		
		.swiper-pagination-bullet {
			width: rem(45);
			height: rem(45);
			display: flex;
			align-items: center;
			opacity: 1;
			justify-content: center;

			color: white;
			font-size: rem(24);
			font-weight: bold;
			border-radius: 50%;
			background-color: #C4C4C4;

			margin-bottom: rem(14) !important;

			@include min540 {
				font-size: rem(18);
			}

			&:hover {
				background: var(--c-green-g);
			}
		}

		.swiper-pagination-bullet-active {
			background: var(--c-green-g);
		}

		ul {
			display: flex;
			flex-wrap: wrap;

			li {
				margin-right: rem(22);

				&:last-child {
					margin-right: 0;
				}
			}

			li.active {
				a {
					background: var(--c-green-g);
				}
			}

			a {
				width: rem(45);
				height: rem(45);
				display: flex;
				align-items: center;
				justify-content: center;

				color: white;
				font-size: rem(24);
				font-weight: bold;
				border-radius: 50%;
				background-color: #C4C4C4;

				@include min540 {
					font-size: rem(18);
				}

				&:hover {
					background: var(--c-green-g);
				}
			}
		}
	}
}